import { Injectable } from '@angular/core';
import Dexie from 'dexie';
import {ClientModel} from './inventory/models/client.model';

@Injectable({
  providedIn: 'root'
})
export class DexieService extends Dexie {

  gebaeude: Dexie.Table<DBDIGebaeude, number>;
  hersteller: Dexie.Table<DBDIHersteller, number>;
  inventar: Dexie.Table<DBDIInventar, number>;
  inventuren: Dexie.Table<DBDIInventuren, number>;
  lieferant: Dexie.Table<DBDILieferant, number>;
  logInventar: Dexie.Table<DBDILogInventar, number>;
  katalog: Dexie.Table<DBDILogObjektGlobal, number>;
  mkatalog: Dexie.Table<DBDILogObjektMandant, number>;
  logRaeume: Dexie.Table<DBDILogRaeume, number>;
  mandanten: Dexie.Table<DBDIMandanten, number>;
  objektKatalogGlobal: Dexie.Table<DBDIObjektKatalogGlobal, number>;
  objektKatalogMandant: Dexie.Table<DBDIObjektKatalogMandant, number>;
  raeume: Dexie.Table<DBDIRaeume, number>;
  uploads: Dexie.Table<DBDIUploads, number>;
  users: Dexie.Table<DBDIUsers, number>;

  constructor() {
     super('LocalInventory');
     this.version(1).stores({
       gebaeude:
         '++gid,mid,mandanten_id,Gebaeude,Adresse,' +
         'created_at,modified_at,created_uid,modified_uid',
       hersteller:
         '++hid,Hersteller,created_at,updated_at',
       inventar:
         'ivid,mcid,rid,Bezeichnung,Typ,Kategorie,Farbe,Groesse,Zustand,Seriennr,' +
         'jobid,invid,iv_nr,ErsteAufnahmeAm,LetzteAufnahmeAm,' +
         'created_at,modified_at,created_uid,modified_uid',
       inventuren:
         '++jobid,mid,gid,Titel,Start,aktiviert,created_at,modified_at,created_uid,modified_uid',
       lieferant:
         '++hid,Lieferant,created_at,updated_at',
       logInventar:
         '++id,crud_action,ivid,mcid,rid,mid,jobid,Bezeichnung,data,' +
         'log_time,log_user',
       logObjektGlobal:
         '++id,crud_action,gcid,Bezeichnung,data,log_time,log_user',
       logObjektMandant:
         '++id,crud_action,gcid,mcid,mid,data,log_time,log_user',
       logRaeume:
         '++id,crud_action,rid,gid,mid,Raum,Raumbezeichnung,Etage,data,' +
         'log_time,log_user,created_at,updated_at',
       mandanten:
         '++mid,Mandant,created_at,modified_at,created_uid,modified_uid',
       objektKatalogGlobal:
         '++gcid,hid,lid,Bezeichnung,Produktnr,Typ,Gruppe,Kategorie,Farbe,Groesse,' +
         'Bild,AnlagenNr,FibuNr,Flaeche,Gewicht,Baujahr,Kst,' +
         'created_at,modified_at,created_uid,modified_uid',
       objektKatalogMandant:
         '++mcid,gcid,mid,created_at,modified_at,created_uid,modified_uid',
       raeume:
         '++rid,gid,raumid,Raum,Raumbezeichnung,Etage,' +
         'created_at,modified_at,created_uid,modified_uid',
       uploads:
         '++id,mid,standort,importkey,filename,filesize,checksum,content,stat,errors,created_at,created_uid,modified_at,modified_uid',
       users:
         '++id,name,email,password,remember_token,created_at,updated_at',
     });
  }
}

